    font-family: 'Roboto';
    src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

* {
    box-sizing: border-box;
}

body {
    margin: auto;
    font-family: 'Roboto'; 
    align-content: center;  
    background-color: rgb(244, 244, 244);
}

h1, h2 {
    margin: 0px;
}

.page-container {
    display: flex;
    background-color: white;
    border-radius: 1rem;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;;
    padding: 2rem;
    margin: auto;
    width: 80vw;
    height: 90vh;
}

nav {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    align-items: center;
    padding: 2rem;
    width: 20%;
}

.nav-btns {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

#project-list {
    display: flex;
    flex-direction: column;
    margin-top: 1rem;
    gap: .5rem;

    & button {
        background-color: transparent;
        border: none;
    }

    & button:hover {
        font-weight: bold;
    }
}

main {
    width: 80%;
    padding: 2rem;
}

header {
    display: flex;
    justify-content: space-between;
    height: 10%;
    padding-bottom: 1rem;
}

#content {
    overflow-y: scroll;
    height: 90%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 2rem;
}

form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 1.5rem;
}

label {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: .5rem;
}

input, select, textarea {
    width: 300px;
}

#tasks-wrapper { 
    display: flex;
    flex-direction: column;
    min-width: 40vw;
    gap: 2rem;
    padding-top: 2rem;
    padding-bottom: 2rem;

    & input {
        width: 1rem;
    }


}

.task {
    display: flex;
    border: black solid;
    justify-content: space-evenly;
    padding: .5rem;
}

.task-completed {
    text-decoration: line-through;
    color: rgba(0, 0, 0, 0.298); 
}
